import CryptoJS from 'crypto-js';

const key = CryptoJS.enc.Utf8.parse('8080808080808080');
const iv = CryptoJS.enc.Utf8.parse('8080808080808080');

const option = {
    keySize: 128 / 8,
    iv: iv,
    mode: CryptoJS.mode.CBC,
    padding: CryptoJS.pad.Pkcs7
};

export default class StorageService {
    constructor() {
        this.option = option;
        this.items = {};
    }

    _decrypt(value) {
        if (!value || value == null) return "";

        const decrypted = CryptoJS.AES.decrypt(value, key, this.option);
        let result = decrypted.toString(CryptoJS.enc.Utf8);
        let obj
        
        try {
            obj = JSON.parse(result);
        } catch (ex) {
            obj = result
        }

        try {
            if (typeof result == 'object') {
                result = eval(obj);
                if (result == undefined) //ie
                    result = obj;
            } else
                result = obj;
        } catch (e) {
            result = obj;
        };

        return result;
    }

    _encrypt(value) {
        const text = JSON.stringify(value);
        const encryptedlogin = CryptoJS.AES.encrypt(text, key, this.option);
        return encryptedlogin.toString();
    }

    getItem(key, copy) {
        if (this.items[key])
            return copy ? Object.assign({}, this.items[key]) : this.items[key];
        else {
            const value = window.localStorage.getItem(key);
            let decrypted = null;

            if (value !== null) {
                decrypted = this._decrypt(value);
                this.items[key] = decrypted;
            }

            return decrypted;
        }
    }

    getItemEncrypted(key) {
        return window.localStorage.getItem(key);
    }

    setItem(key, value) {
        if (typeof this.items[key] != 'undefined')
            delete this.items[key];

        if (value !== '') {
            const encrypted = this._encrypt(value);
            window.localStorage.setItem(key, encrypted);
        }
    }

    removeItem(key) {
        if (typeof this.items[key] != 'undefined')
            delete this.items[key];

        window.localStorage.removeItem(key);
    }

    clear() {
        this.items = {};
        window.localStorage.clear();
    }
}
